import React from 'react'
import Navheader from './Navheader'
import Button from '@material-ui/core/Button';

function Presentation() {
    return (
        <section className="Presentationsection">
            <Navheader />
            <div className="Presentation">
            <div className="item2">
                <article>
                Hello,<br></br>Je suis CHidiac<br></br>Administrateur Réseaux <br></br> développeur freelance à temps partiel
                </article>
                <Button className="buttpresentation" variant="contained" color="F48C06">Contact me</Button>
                <Button variant="contained" color="#F48C06" className="buttpresentation">Télécharger mon CV</Button>
            </div>
            <div className="item2">Photo</div>
            </div>
        </section>
    )
}

export default Presentation
